export default {
	data() {
		return {
			showCategoryIndex: 0,
			value:''
		}
	},
	props: {
		categoryList:{
			type:Array
		},
		backgroundColor:{
			type:String,
			default:"#ffffff",
		}
	},
	methods:{
		handleCategory(index) {
			this.showCategoryIndex = index;
		},
		selectCategory(item){
			this.value=item.id;
			this.$emit('update-value',item);
		}
	}
}
